.wrpArrow{
    display: flex;
    justify-content:center;
    
}
.wrpArrow img{
    height:40px;
    overflow-x: hidden;
    animation:animateDown infinite 1.5s;
}
.wrpArrow img:hover{
    cursor:pointer;

}

@keyframes animateDown{
    0%,20%,50%,80%,100%{
      transform: translateY(0);
    }
    40%{
      transform: translateY(5px);
    }
    60%{
      transform: translateY(3px);
    }
  }